@font-face {
  font-family: "Inter", sans-serif;
  src: url(../fonts/Inter-VariableFont_slntwght.ttf);
}
:root {
  font-family: "Inter", sans-serif;
  --fw-regular: 400;
  --fw-bold: 700;
  --fw-bolder: 900;
  --clr-red-50: hsl(5, 85%, 63%);
  --clr-orange-50: hsl(35, 77%, 62%);
  --clr-white: hsl(36, 100%, 99%);
  --clr-blue-10: hsl(233, 8%, 79%);
  --clr-blue-50: hsl(236, 13%, 42%);
  --clr-blue-90: hsl(240, 100%, 5%);
}

/*========== Mixin and Functions ============*/
/*+++++++ CSS Reset ++++++++++++*/
/* Box sizing rules */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  width: 100%;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.2;
  overflow-y: 0;
}

/* A elements that don't have a class get default styles */
a,
a:not([class]) {
  color: inherit;
  text-decoration: none;
}

/* Make images easier to work with */
img,
picture {
  height: 100%;
  max-width: 100%;
  display: block;
}

button {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*++++++++++++++++++++++++++++++++++++++++++++++++++++++*/
body {
  background-color: var(--clr-white);
  font-size: 0.9375rem;
  color: var(--clr-blue-90);
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.hidden {
  display: none;
}

.backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
  z-index: 90;
}

.root {
  width: 90%;
  min-height: 100vh;
  margin-inline: auto;
  padding-block-start: 1.875rem;
  padding-block-end: 3.75rem;
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  grid-template-rows: auto 1fr;
  place-content: center;
  position: relative;
}

header {
  width: 100%;
  margin-block-end: 1.875rem;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  outline: 0px dotted var(--clr-red-50);
  outline-offset: 0.5rem;
  transition: all 150ms ease-in-out;
}
.header__logo:where(:hover, :focus) {
  outline-width: 3px;
}
.header__navbar .main-nav {
  transition: all 250ms ease-in-out;
}
.header__navbar .main-nav ul {
  display: flex;
}
.header__navbar .main-nav .nav-link {
  display: inline-block;
  padding: 0.25rem 0.9375rem;
  font-size: 1.125rem;
  font-weight: var(--fw-bold);
  color: var(--clr-blue-50);
  transition: color 250ms ease;
}
.header__navbar .main-nav .nav-link:where(:hover, :focus) {
  color: var(--clr-red-50);
  outline: 1px dotted var(--clr-red-50);
}
.header__navbar .nav-toggle {
  display: none;
  position: relative;
  z-index: 100;
}
.header__navbar .nav-toggle [aria-label] {
  padding: 0.3125rem;
  border: 3px solid black;
}
.header__navbar [aria-label=nav-toggle-close] {
  position: fixed;
  width: 48px;
  height: 48px;
  top: 4%;
  right: 5%;
}
@media only screen and (max-width: 62.99em) {
  .header__navbar .main-nav {
    background-color: var(--clr-white);
    width: 70%;
    height: 100vh;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 99;
    transform: translateX(100%);
    opacity: 0;
    transform-origin: center;
    visibility: hidden;
    pointer-events: none;
  }
  .header__navbar ul {
    flex-direction: column;
    margin-block-start: min(6.25rem, 25vh);
    margin-inline-start: 1.25rem;
  }
  .header__navbar ul > * + * {
    margin-top: 1.25rem;
  }
  .header__navbar .active.main-nav {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
  .header__navbar .nav-toggle {
    display: block;
  }
}

.hero {
  width: 100%;
  height: fit-content;
  display: grid;
  align-content: baseline;
  gap: 2.5rem;
}
.hero__main {
  width: 100%;
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  grid-template-rows: auto;
  gap: 2.5rem;
}
@media only screen and (min-width: 63em) {
  .hero__main {
    grid-template-columns: 2fr 1fr;
  }
}
.hero__main__section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero__main__section .section-details {
  display: flex;
  flex-direction: column;
  gap: var(--gap, 1.25rem);
  margin-block-start: 1.25rem;
}
@media only screen and (min-width: 44em) {
  .hero__main__section .section-details {
    gap: 2.5rem;
    flex-direction: row;
  }
}
.hero__main__section .section-details h1 {
  font-size: clamp(2.25rem, 3vw, 3rem);
  line-height: 1;
  font-weight: var(--fw-bolder);
}
.hero__main__section .section-article {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero__main__section .section-article article {
  line-height: 1.5;
  color: var(--clr-blue-50);
  margin-block-end: 0.9375rem;
}
.hero__main__section .section-article button {
  width: fit-content;
  appearance: none;
  cursor: pointer;
  border: none;
  font-weight: var(--fw-bold);
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 0.625rem 1.25rem;
  background-color: var(--clr-red-50);
  color: var(--clr-white);
  transition: background-color 250ms ease;
}
.hero__main__section .section-article button:where(:hover, :focus) {
  background-color: var(--clr-blue-90);
}
.hero__main__aside {
  display: grid;
  place-content: center;
  padding: 1.875rem 1.25rem;
  background-color: var(--clr-blue-90);
  color: var(--clr-blue-10);
}
.hero__main__aside > * + * {
  margin-block-start: 1.25rem;
}
.hero__main__aside h1 {
  font-size: 1.875rem;
  color: var(--clr-orange-50);
}
.hero__main__aside .article {
  padding-block-end: 1.875rem;
  border-bottom: 2px solid var(--clr-blue-50);
  margin-block-end: 1.5625rem;
}
.hero__main__aside .article:last-child {
  border: none;
  padding: 0;
  margin-block-end: 0;
}
.hero__main__aside .article a h2 {
  font-size: 1.125rem;
  font-weight: var(--fw-bolder);
  margin-block-end: 0.9375rem;
}
.hero__main__aside .article a:where(:hover, :focus) {
  color: var(--clr-orange-50);
}
.hero__main__aside .article p {
  color: var(--clr-blue-10);
}
.hero .carousel {
  width: 100%;
  height: min-content;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: row;
  gap: 1.25rem;
  flex-wrap: wrap;
}
@media only screen and (min-width: 44em) {
  .hero .carousel {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media only screen and (min-width: 63em) {
  .hero .carousel {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.hero .carousel > * {
  flex: 1;
}
.hero .carousel__section {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: min-content;
  gap: calc(0.9375rem + 0.4vw);
}
@media only screen and (min-width: 44em) {
  .hero .carousel__section {
    grid-template-columns: 7.5rem 2fr;
  }
}
.hero .carousel__section__article {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-block-end: 0;
}
.hero .carousel__section__article h1 {
  color: var(--clr-blue-10);
  font-size: clamp(1.75rem, 5vw, 2rem + 0.4vw);
}
.hero .carousel__section__article a h2 {
  font-size: clamp(1.125rem, 2vw, 1.5rem);
}
.hero .carousel__section__article a:where(:hover, :focus) {
  color: var(--clr-red-50);
}
.hero .carousel__section__article p {
  color: var(--clr-blue-50);
}